}
.menu-opener {
  cursor: pointer;
  width: 4rem;
  height: 4rem;
  position: absolute;
  top: 45px;
  right: 15px;
  user-select: none;
}
.menu-opener-inner {
  background: #000;
  height: .5rem;
  margin-left: .75rem;
  margin-top: 1.75rem;
  width: 2.5rem;
}
.menu-opener-inner::before,
.menu-opener-inner::after {
  background: #000;
  content: '';
  display: block;
  height: .5rem;
  width: 2.5rem;
}
.menu-opener-inner::before {
  transform: translateY(-0.75rem);
}
.menu-opener-inner::after {
  transform: translateY(0.25rem);
}
.menu-opener-inner.active {
  background: transparent;
}
.menu-opener-inner.active::before {
  transform: translateY(0rem) rotate(-45deg);
}
.menu-opener-inner.active::after {
  transform: translateY(-0.5rem) translateX(0rem) rotate(45deg);
}
.menu-opener,
.menu-opener:hover,
.menu-opener.active,
.menu-opener-inner,
.menu-opener-inner::before,
.menu-opener-inner::after,
.menu,
.menu.active {
  -webkit-transition: 250ms all;
  transition: 250ms all;
}
figure.blogs__item img {
  max-width: 100%;
  backface-visibility: hidden;
  vertical-align: top;
  filter: grayscale(100%);
}
figure.blogs__item .title {
  position: absolute;
  top: 58%;
  left: 40px;
  padding: 15px 15px;
}
figure.blogs__item .title:before,
figure.blogs__item .title:after {
  height: 2px;
  width: 400px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}
figure.blogs__item .title:before {
  top: 0;
  left: 15px;
  transform: translateX(100%);
}
figure.blogs__item .title:after {
  bottom: 0;
  right: 15px;
  transform: translateX(-100%);
}
figure.blogs__item .title div:before,
figure.blogs__item .title div:after {
  width: 2px;
  height: 300px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}
figure.blogs__item .title div:before {
  top: 15px;
  right: 0;
  transform: translateY(100%);
}
figure.blogs__item .title div:after {
  bottom: 15px;
  left: 0;
  transform: translateY(-100%);
}
figure.blogs__item h2,
figure.blogs__item h4 {
  margin: 0;
  text-transform: uppercase;
}
figure.blogs__item h2 {
  font-weight: 400;
}
figure.blogs__item h4 {
  display: block;
  font-weight: 400;
  padding: 10px 10px 0px;
}
figure.blogs__item figcaption {
  position: absolute;
  bottom: 42%;
  left: 45px;
  text-align: left;
  opacity: 0;
  padding: 5px 55px 10px 10px;
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 20px;
  font-family: "Arial";
}
figure.blogs__item figcaption p {
