  background: rgba(0, 0, 0, 0.4);
  padding: 0 5px;
}
figure.blogs__item a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
figure.blogs__item:hover img,
figure.blogs__item.hover img {
  zoom: 1;
  filter: none;
  opacity: 0.8;
}
figure.blogs__item:hover .title:before,
figure.blogs__item.hover .title:before,
figure.blogs__item:hover .title:after,
figure.blogs__item.hover .title:after,
figure.blogs__item:hover .title div:before,
figure.blogs__item.hover .title div:before,
figure.blogs__item:hover .title div:after,
figure.blogs__item.hover .title div:after {
  transform: translate(0, 0);
}
figure.blogs__item:hover .title:before,
figure.blogs__item.hover .title:before,
figure.blogs__item:hover .title:after,
figure.blogs__item.hover .title:after {
  transition-delay: 0.15s;
}
figure.blogs__item:hover figcaption,
figure.blogs__item.hover figcaption {
  opacity: 1;
  transition-delay: 0.2s;
}
