  background-image: url(../img/s-od.png);
}
.footer__logo {
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  vertical-align: bottom;
  line-height: 205px;
  text-decoration: none;
  color: #fff;
}
.footer__logo:hover {
  color: #275c9d;
}
.menu-footer {
  color: transparent;
  height: 4rem;
  position: absolute;
  right: 15px;
  top: 0;
  user-select: none;
  width: 0rem;
  z-index: 1;
}
.menu-footer.active {
  width: calc(97.5%);
}
.menu-footer.active .menu-link-footer {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}
.menu-footer.active .menu-link-footer:hover {
  color: #275c9d;
}
.menu-inner-footer {
  display: flex;
  flex-direction: row;
  height: 100%;
  list-style-type: none;
  margin: 0;
  margin-right: 2rem;
  padding: 0;
}
.menu-link-footer {
  color: transparent;
  display: flex;
  flex: 1 1 auto;
  font-size: 2rem;
  font-weight: 700;
  height: 100%;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
li {
  margin: auto;
